.btn {
    position: relative;
    z-index: 1;
    cursor: pointer;
    color: #fff;
    padding: 10px 26px;
    border-radius: 30px;
    font-size: 14px;
    background-color: var(--color-custom);
    border: 1px solid var(--color-custom);
    transition: all ease-in-out .3s;
}
.btn:hover {
    background-color: #1f6b1f;
    /*color: black;*/
    border: 1px solid transparent;
}